.BarGraph-sequence-container {
  position: relative;
  -webkit-font-smoothing: antialiased;
  background-color: grey;
}

.BarGraph-container {
  height: 100vh;
  width: 80vw;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.BarGraph-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.BarGraph-sticky:before,
.BarGraph-sticky:after {
  content: '';
  display: table;
}

.BarGraph-waypoint-buffer {
  height: 100vh;
  background-color: grey;
}

